import { Vec3, vec3, kZeroVec, vec3Key } from 'neuroglancer/util/geom';

/**
 * @param size Size of each chunk in nanometers.
 * @param offset Offset of chunk boundaries relative to
 *   (0,0,0).  It should be the case that 0 >= offset > size.
 */
export class ChunkLayout {
  size: Vec3;
  offset: Vec3;
  constructor(size: Vec3, offset?: Vec3) {
    this.size = vec3.clone(size);
    if (offset === undefined) {
      this.offset = vec3.create();
    } else {
      this.offset = vec3.clone(offset);
    }
  }
  static cache = new Map<string, ChunkLayout>();
  toObject (msg: any) {
    msg['size'] = this.size;
    msg['offset'] = this.offset;
  }

  static get(size: Vec3, offset = kZeroVec) {
    let cache = ChunkLayout.cache;
    let key = `${vec3Key(size)}+${vec3Key(offset)}`;
    let obj = cache.get(key);
    if (obj === undefined) {
      obj = new ChunkLayout(size, offset);
      cache.set(key, obj);
    }
    return obj;
  }
  static fromObject (msg: any) {
    return ChunkLayout.get(msg['size'], msg['offset']);
  }
};



